<template >
  <a-row type="flex" justify="end" style="min-width: 1400px">
    <a-col style="width: 85%">
      <div>
        <div class="mlist-control ant-list-bordered">
          <div class="list-control-finish" >
            <a-form layout="inline"  @submit="handleSubmit" :form="form">
              <a-form-item label="选择企业">
                <a-select style="width: 200px" :value="enterName" @change="handleEnterNameChange">
                  <a-select-option value="全部">全部</a-select-option>
                  <a-select-option v-for="item in entList" :key="item" :value="item.enterName">{{item.enterName}}</a-select-option>
                </a-select>
              </a-form-item>
              <a-form-item label="需求状态" >
                <a-select style="width: 200px" :value="needsType" @change="handleTypeChange">
                  <a-select-option value="全部">全部</a-select-option>
                  <a-select-option value="匹配中">匹配中</a-select-option>
                  <a-select-option value="匹配完成待支付">匹配完成待支付</a-select-option>
                  <a-select-option value="交易成功">交易成功</a-select-option>
                  <a-select-option value="匹配失败">匹配失败</a-select-option>
                </a-select>
              </a-form-item>
            </a-form>
          </div>
        </div>
        <a-list
          class="listpage"
          :bordered="true"
          :pagination="paginationProps"
          :loading="loading"
          itemLayout="horizontal"
          :dataSource="data"
          :style="{backgroundImage: 'url(' + img + ')',
          backgroundRepeat:no-repeat,center,top,
          backgroundSize:'100% 100%'
          }"
        >
          <a-list-item slot="renderItem" slot-scope="item">
            <a-button type="primary" slot="actions" @click="getDemandDetail(item.id)">查看</a-button>

            <a-list-item-meta :description="item.needsDes">
              <a slot="title">{{item.needsFarmerType}}</a>
              <a-avatar slot="avatar" shape="square" style="width:50px;height:50px;padding-top: 25px;" :style="machineStatusStyleAvatar(item.needsType)" >Task</a-avatar>
            </a-list-item-meta>

            <span class="ant-list-item-meta-description" style="width:150px;margin-right:30px">
              发布时间
              <br />
              {{ item.needsTime }}
            </span>
            <span class="ant-list-item-meta-description" style="width:150px;margin-right:30px">
              开工时间
              <br />
              {{ item.needsBeginTime }}
            </span>
            <span
              :style="machineStatusStyle(item.needsType)"
              class="ant-list-item-meta-description"
              style="display:inline-block;width:200px;align-self:center"
            >{{ item.needsType }}</span>
          </a-list-item>

          <br />
        </a-list>

        <a-layout-footer style="textAlign: center">DDDB ©2019 Created by 六芒星</a-layout-footer>
      </div>
    </a-col>
  </a-row>
</template>
<script>
import { getAllNeeds,getEnterpriseList } from "../../api/manRequest";
//import reqwest from 'reqwest'

export default {
  data() {
    return {
      id: localStorage.getItem("userId"),
      loading: false,
      loadingMore: false,
      showLoadingMore: true,
      enterName: "全部",
      needsType: "全部",
      entList: [],
      needList: [],
      filterList: [],
      img:require('../../assets/dddb-webEnt/outbg.jpg')
    };
  },
  created() {
    this.$parent.showMenu = false;
    this.$parent.showSiderMenu = true;
  },
  computed: {
    list() {
      // return this.$store.state.taskList;
    },
    paginationProps() {
      let pag = {
        pageSize: 6,
        total: this.data.length
      };
      return pag;
    },
    data() {
      let list = [...this.needList];
      if (this.needsType === "匹配中") {
        list = list.filter(item => item.needsType == "匹配中");
      } else if (this.needsType === "匹配完成待支付") {
        list = list.filter(item => item.needsType == "匹配完成待支付");
      } else if (this.needsType === "交易成功") {
        list = list.filter(item => item.needsType == "交易成功");
      } else if (this.needsType === "匹配失败") {
        list = list.filter(item => item.needsType == "匹配失败");
      }
      if (this.enterName != "全部") {
        list = list.filter(item => item.enterName == this.enterName)
      }
      return list;
    }
  },
  mounted() {
    this.getEnterpriseList();
    this.getDemandsList();
  },
  methods: {
    // 动态样式
    machineStatusStyleAvatar(e){
      if(e == '匹配中'){
        return {
          'background-color':'#D39846'
        }
      }else if(e == '匹配失败'){
        return {
          'background-color':'#ABABAA'
        }
      }else if(e == '匹配完成待支付'){
        return {
          'background-color':'#314659'
        }
      }else if(e == '交易成功'){
        return {
          'background-color':'#4F5F46'
        }
      };
    },
    machineStatusStyle(e){
      if(e == '匹配中'){
        return {
          'color':'#D39846'
        }
      }else if(e == '匹配失败'){
        return {
          'color':'#ABABAA'
        }
      }else if(e == '匹配完成待支付'){
        return {
          'color':'#314659'
        }
      }else if(e == '交易成功'){
        return {
          'color':'#4F5F46'
        }
      };
    },
    getEnterpriseList() {
      getEnterpriseList()
        .then(response => {
          this.entList = response.data.data;
          console.log(this.entList);
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    getDemandsList() {
      getAllNeeds()
        .then(response => {
          this.needList = response.data.data;
          console.log(this.needList);
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    // 选择
    handleTypeChange(e) {
      this.needsType = e;
    },
    handleEnterNameChange(e) {
      this.enterName = e;
    },
    onSearch() {},
    getDemandDetail(itemId) {
      this.$router.push({
        name: "ManDemandDetail",
        query: {
          id: itemId
        }
      });
    }
  }
};
</script>
<style scoped>
.list-control {
  background: #fff;
  width: 90%;
  margin: 0 auto;
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 16px;
  height: 60px;
  display: flex;
  flex-direction: row;
}
/* .list-control-info {
  display: inline-block;
  align-self: center;
  margin-right: 10px;
} */
.list-control-finish {
  margin-left: 50px;
}

.listpage {
  min-width: 1200px;
  min-height: 500px;
  background: #fff;
  margin: 0 auto;
  margin-bottom: 20px;
  padding: 16px 40px 0px 40px;
  width: 90%;
}
.Content {
  display: flex;
  flex-direction: row;
  justify-content: center;
}
</style>



